/** Elements */
.theme--light.v-data-table tbody tr:hover:not(.v-data-table__expand-row) {
  background: #eeeeee;
}
.theme--light.v-data-table tbody tr.expanded {
  border-bottom: 0;
}
.theme--light.v-data-table tbody tr.expanded.expanded__row {
  background: #f5f5f5;
}
.theme--light.v-data-table tbody tr.expanded.expanded__content {
  box-shadow: inset 0px 4px 8px -5px rgba(50, 50, 50, 0.75), inset 0px -4px 8px -5px rgba(50, 50, 50, 0.75);
}
.theme--light.v-data-table .v-data-footer {
  border-top: 1px solid rgba(0, 0, 0, 0.12);
}

.theme--dark.v-data-table tbody tr:hover:not(.v-data-table__expand-row) {
  background: #616161;
}
.theme--dark.v-data-table tbody tr.expanded {
  border-bottom: 0;
}
.theme--dark.v-data-table tbody tr.expanded.expanded__row {
  background: #505050;
}
.theme--dark.v-data-table tbody tr.expanded.expanded__content {
  box-shadow: inset 0px 4px 8px -5px rgba(50, 50, 50, 0.75), inset 0px -4px 8px -5px rgba(50, 50, 50, 0.75);
}
.theme--dark.v-data-table .v-data-footer {
  border-top: 1px solid rgba(255, 255, 255, 0.12);
}

.v-data-table__mobile-row {
  display: block;
}
.v-data-table__mobile-row__wrapper {
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.v-data-table__mobile-row__header {
  font-weight: 600;
}
.v-data-table__mobile-row__cell {
  text-align: right;
}

.v-data-table__expand-row {
  border: none !important;
}
.v-data-table__expand-col {
  padding: 0 !important;
  height: 0px !important;
}
.v-data-table__expand-col--expanded {
  border-bottom: 1px solid rgba(0, 0, 0, 0.12);
}
.v-data-table__expand-content {
  transition: height 0.3s cubic-bezier(0.25, 0.8, 0.5, 1);
}
.v-data-table__expand-content > .card {
  border-radius: 0;
  box-shadow: none;
}

.v-row-group__header, .v-row-group__summary {
  background: lightgrey;
}
.v-row-group__header td, .v-row-group__summary td {
  height: 35px;
}

.v-data-table__expand-icon {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: pointer;
}
.v-data-table__expand-icon--active {
  -webkit-transform: rotate(-180deg);
          transform: rotate(-180deg);
}