.v-list-group .v-list-item__icon.v-list-group__header__append-icon {
  min-width: 48px;
  justify-content: flex-end;
  margin-left: 0;
}

.v-list-group--sub-group {
  align-items: center;
  display: flex;
  flex-wrap: wrap;
}

.v-list-group__header {
  align-items: center;
  cursor: pointer;
  display: flex;
  flex: 1 1 auto;
  outline: none;
  padding: 0 16px;
  position: relative;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.v-list-group__header > .v-list-item {
  padding: 0;
}

.v-list-group__items {
  flex: 1 1 auto;
}

.v-list-group--active > .v-list-group__header > .v-list-group__header__append-icon .v-icon {
  -webkit-transform: rotate(-180deg);
          transform: rotate(-180deg);
}
.v-list-group--active > .v-list-group__header.v-list-group__header--sub-group > .v-list-group__header__prepend-icon .v-icon {
  -webkit-transform: rotate(-180deg);
          transform: rotate(-180deg);
