/** Elements */
.theme--light.v-card {
  background-color: #FFFFFF;
  color: rgba(0, 0, 0, 0.87);
}
.theme--light.v-card .v-card__text {
  color: rgba(0, 0, 0, 0.54);
}
.theme--light.v-card.v-card--outlined {
  border: 1px solid rgba(0, 0, 0, 0.12);
}

.theme--dark.v-card {
  background-color: #424242;
  color: #FFFFFF;
}
.theme--dark.v-card .v-card__text {
  color: rgba(255, 255, 255, 0.7);
}
.theme--dark.v-card.v-card--outlined {
  border: 1px solid rgba(255, 255, 255, 0.12);
}

.v-card {
  outline: none;
  text-decoration: none;
  transition-property: box-shadow, opacity;
  box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2), 0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);
}
.v-card > *:first-child:not(.v-btn):not(.v-chip),
.v-card > .v-card__progress + *:not(.v-btn):not(.v-chip) {
  border-top-left-radius: inherit;
  border-top-right-radius: inherit;
}
.v-card > *:last-child:not(.v-btn):not(.v-chip) {
  border-bottom-left-radius: inherit;
  border-bottom-right-radius: inherit;
}

.v-card__progress {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  overflow: hidden;
}
.v-card__progress > .v-progress-linear {
  margin: 0;
}

.v-card__title {
  align-items: center;
  display: flex;
  flex-wrap: wrap;
  font-size: 1.5rem;
  font-weight: 400;
  letter-spacing: normal;
  line-height: 2rem;
  padding: 16px 16px 8px;
}
.v-card__title + .v-card__text {
  padding-top: 0;
}

.v-card__text {
  font-size: 0.875rem;
  line-height: 1.375rem;
  letter-spacing: 0.0071428571em;
  padding: 16px;
  width: 100%;
}

.v-card__actions {
  align-items: center;
  display: flex;
  padding: 8px;
}
.v-card__actions .v-btn.v-size--default {
  padding: 0 8px;
}
.v-card__actions .v-btn.v-size--default + .v-btn {
  margin-left: 8px;
}
.application--is-rtl .v-card__actions .v-btn.v-size--default + .v-btn {
  margin-left: 0;
  margin-right: 8px;
}

.v-card--flat {
  box-shadow: 0px 0px 0px 0px rgba(0, 0, 0, 0.2), 0px 0px 0px 0px rgba(0, 0, 0, 0.14), 0px 0px 0px 0px rgba(0, 0, 0, 0.12);
}

.v-card--hover {
  cursor: pointer;
  transition: box-shadow 0.4s cubic-bezier(0.25, 0.8, 0.25, 1);
}
.v-card--hover:hover {
  box-shadow: 0px 5px 5px -3px rgba(0, 0, 0, 0.2), 0px 8px 10px 1px rgba(0, 0, 0, 0.14), 0px 3px 14px 2px rgba(0, 0, 0, 0.12);
}

.v-card--link {
  cursor: pointer;
}
.v-card--link .v-chip {
  cursor: pointer;
}
.v-card--link:focus:before {
  opacity: 0.08;
}
.v-card--link:before {
  background: currentColor;
  bottom: 0;
  content: "";
  left: 0;
  opacity: 0;
  position: absolute;
  right: 0;
  top: 0;
  transition: 0.2s opacity;
}

.v-card--disabled {
  opacity: 0.6;
  pointer-events: none;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.v-card--outlined {
  box-shadow: none;
}

.v-card--raised {
  box-shadow: 0px 5px 5px -3px rgba(0, 0, 0, 0.2), 0px 8px 10px 1px rgba(0, 0, 0, 0.14), 0px 3px 14px 2px rgba(0, 0, 0, 0.12);
}