/** Elements */
.theme--light.v-data-table-header th.sortable .v-icon {
  color: rgba(0, 0, 0, 0.38);
}
.theme--light.v-data-table-header th.sortable:hover {
  color: rgba(0, 0, 0, 0.87);
}
.theme--light.v-data-table-header th.sortable.active {
  color: rgba(0, 0, 0, 0.87);
}
.theme--light.v-data-table-header th.sortable.active .v-icon {
  color: rgba(0, 0, 0, 0.87);
}
.theme--light.v-data-table-header__sort-badge {
  background-color: rgba(0, 0, 0, 0.12);
  color: rgba(0, 0, 0, 0.87);
}

.theme--dark.v-data-table-header th.sortable .v-icon {
  color: rgba(255, 255, 255, 0.5);
}
.theme--dark.v-data-table-header th.sortable:hover {
  color: white;
}
.theme--dark.v-data-table-header th.sortable.active {
  color: white;
}
.theme--dark.v-data-table-header th.sortable.active .v-icon {
  color: white;
}
.theme--dark.v-data-table-header__sort-badge {
  background-color: rgba(255, 255, 255, 0.12);
  color: #FFFFFF;
}

.v-data-table-header th.sortable {
  pointer-events: auto;
  cursor: pointer;
  outline: 0;
}
.v-data-table-header th.sortable i {
  font-size: 18px;
  display: inline-block;
  opacity: 0;
  transition: 0.3s cubic-bezier(0.25, 0.8, 0.5, 1);
}
.v-data-table-header th.sortable:focus i, .v-data-table-header th.sortable:hover i {
  opacity: 0.6;
}
.v-data-table-header th.active {
  -webkit-transform: none;
          transform: none;
}
.v-data-table-header th.active i {
  opacity: 1;
}
.v-data-table-header th.active.desc i {
  -webkit-transform: rotate(-180deg);
          transform: rotate(-180deg);
}

.v-data-table-header__sort-badge {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  border: 0px;
  border-radius: 50%;
  min-width: 18px;
  min-height: 18px;
  height: 18px;
  width: 18px;
}

.v-data-table-header-mobile th {
  height: initial;
}

.v-data-table-header-mobile__wrapper {
  display: flex;
}
.v-data-table-header-mobile__wrapper .v-simple-checkbox {
  margin-right: 24px;
}
.v-data-table-header-mobile__wrapper .v-select {
  margin-bottom: 8px;
}