/** Elements */
.v-data-footer {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  align-items: center;
  font-size: 12px;
}
.v-data-footer .v-btn {
  color: inherit;
}

.v-data-footer__icons-before .v-btn:last-child {
  margin-right: 7px;
}

.v-data-footer__icons-after .v-btn:first-child {
  margin-left: 7px;
}

.v-data-footer__pagination {
  display: block;
  text-align: center;
  margin: 0 32px 0 24px;
}

.v-data-footer__select {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  margin-right: 14px;
  white-space: nowrap;
}
.v-data-footer__select .v-select {
  flex: 0 1 0;
  margin: 13px 0 13px 34px;
  padding: 0;
  position: initial;
}
.v-data-footer__select .v-select__selections {
  flex-wrap: nowrap;
}
.v-data-footer__select .v-select__selections .v-select__selection--comma {
  font-size: 12px;
}