/** Elements */
.theme--light.v-btn-toggle .v-btn.v-btn {
  border-color: rgba(0, 0, 0, 0.12) !important;
}
.theme--light.v-btn-toggle .v-btn.v-btn:focus:not(:active) {
  border-color: rgba(0, 0, 0, 0.26);
}
.theme--light.v-btn-toggle .v-btn.v-btn .v-icon {
  color: #000000;
}

.theme--dark.v-btn-toggle .v-btn.v-btn {
  border-color: rgba(255, 255, 255, 0.12) !important;
}
.theme--dark.v-btn-toggle .v-btn.v-btn:focus:not(:active) {
  border-color: rgba(255, 255, 255, 0.3);
}
.theme--dark.v-btn-toggle .v-btn.v-btn .v-icon {
  color: #FFFFFF;
}

.v-btn-toggle {
  border-radius: 2px;
  display: inline-flex;
}
.v-btn-toggle > .v-btn.v-btn {
  border-radius: 0;
  border-style: solid;
  border-width: thin;
  box-shadow: none;
  min-height: 0;
  min-width: 48px;
  padding: 0 12px;
  height: 48px;
}
.v-btn-toggle > .v-btn.v-btn:first-child {
  border-top-left-radius: inherit;
  border-bottom-left-radius: inherit;
}
.v-btn-toggle > .v-btn.v-btn:last-child {
  border-top-right-radius: inherit;
  border-bottom-right-radius: inherit;
}
.v-btn-toggle > .v-btn.v-btn:after {
  display: none;
}

.v-btn-toggle--rounded {
  border-radius: 24px;
}