/** Ripples */
/** Elements */
.theme--light.v-tabs .v-tab:not(.v-tab--active),
.theme--light.v-tabs .v-tab:not(.v-tab--active) > .v-icon,
.theme--light.v-tabs .v-tab--disabled {
  color: rgba(0, 0, 0, 0.54);
}

.theme--dark.v-tabs .v-tab:not(.v-tab--active),
.theme--dark.v-tabs .v-tab:not(.v-tab--active) > .v-icon,
.theme--dark.v-tabs .v-tab--disabled {
  color: rgba(255, 255, 255, 0.6);
}

.v-tabs-bar.theme--dark .v-tab,
.v-tabs-bar.theme--dark .v-tabs-slider-wrapper {
  color: #FFFFFF;
}
.v-tabs-bar.theme--dark .v-tab:not(.v-tab--active):not(.v-tab--disabled) {
  opacity: 0.7;
}

.v-tabs {
  flex: 1 1 auto;
}
.v-tabs .v-menu__activator {
  height: 100%;
}

.v-tabs-bar {
  border-radius: inherit;
  height: 48px;
}
.v-tabs-bar.v-item-group > * {
  cursor: initial;
}

.v-tab {
  align-items: center;
  display: flex;
  flex: 0 1 auto;
  font-size: 0.875rem;
  font-weight: 500;
  justify-content: center;
  min-width: 90px;
  max-width: 300px;
  outline: none;
  padding: 0 16px;
  position: relative;
  text-decoration: none;
  text-transform: uppercase;
  transition: none;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.v-tab:before {
  background-color: currentColor;
  bottom: 0;
  content: "";
  left: 0;
  opacity: 0;
  position: absolute;
  right: 0;
  top: 0;
  transition: 0.3s cubic-bezier(0.25, 0.8, 0.5, 1);
}
.v-tab:hover:before {
  opacity: 0.16;
}
.v-tab:focus:before {
  opacity: 0.2;
}

.v-tabs-slider {
  background-color: currentColor;
  height: 2px;
  width: 100%;
}
.v-tabs-slider-wrapper {
  bottom: 0;
  margin: 0 !important;
  position: absolute;
  transition: 0.3s cubic-bezier(0.25, 0.8, 0.5, 1);
}

.v-tabs--align-with-title > .v-tabs-bar {
  padding-left: 60px;
}

.v-tabs--fixed-tabs .v-tabs-bar__content > *:last-child,
.v-tabs--centered .v-tabs-bar__content > *:last-child {
  margin-right: auto;
}
.v-tabs--fixed-tabs .v-tabs-bar__content > *:first-child:not(.v-tabs-slider-wrapper),
.v-tabs--centered .v-tabs-bar__content > *:first-child:not(.v-tabs-slider-wrapper) {
  margin-left: auto;
}
.v-tabs--fixed-tabs .v-tabs-slider-wrapper + *,
.v-tabs--centered .v-tabs-slider-wrapper + * {
  margin-left: auto;
}

.v-tabs--fixed-tabs .v-tab {
  flex: 1 1 auto;
  width: 100%;
}

.v-tabs--grow .v-tab {
  flex: 1 0 auto;
  max-width: none;
}

.v-tabs--icons-and-text .v-tabs-bar {
  height: 72px;
}
.v-tabs--icons-and-text .v-tab {
  flex-direction: column-reverse;
}
.v-tabs--icons-and-text .v-tab > *:first-child {
  margin-bottom: 6px;
}

.v-tabs--overflow .v-tab {
  flex: 1 0 auto;
}

.v-tabs--right .v-tab:first-child,
.v-tabs--right .v-tabs-slider-wrapper + .v-tab {
  margin-left: auto;
}
.v-tabs--right .v-tab:last-child {
  margin-right: 0;
}

.v-tabs--vertical {
  display: flex;
}
.v-tabs--vertical .v-tabs-bar {
  height: auto;
}
.v-tabs--vertical .v-tabs-bar__content {
  flex-direction: column;
}
.v-tabs--vertical .v-tab {
  height: 48px;
}
.v-tabs--vertical .v-tabs-slider {
  height: 100%;
}
.v-tabs--vertical .v-window {
  flex: 1 1 auto;
}
.v-tabs--vertical.v-tabs--icons-and-text .v-tab {
  height: 72px;
}

.v-tab--active {
  color: inherit;
}
.v-tab--active .v-icon {
  color: inherit;
}

.v-tab--disabled {
  pointer-events: none;
  opacity: 0.5;
}
.v-tab--disabled:before {
  opacity: 0;
}