/** Ripples */
/** Elements */
.theme--light.v-alert .v-alert--prominent .v-alert__icon:after {
  background: rgba(0, 0, 0, 0.12);
}

.theme--dark.v-alert .v-alert--prominent .v-alert__icon:after {
  background: rgba(255, 255, 255, 0.12);
}

.v-alert {
  display: flex;
  font-size: 16px;
  margin-bottom: 16px;
  padding: 16px;
}
.v-alert > .v-icon,
.v-alert > .v-alert__content {
  margin-right: 16px;
}
.v-alert > .v-icon + .v-alert__content {
  margin-right: 0;
}
.v-alert > .v-alert__content + .v-icon {
  margin-right: 0;
}
.application--is-rtl .v-alert > .v-icon,
.application--is-rtl .v-alert > .v-alert__content {
  margin-right: 0;
  margin-left: 16px;
}
.application--is-rtl .v-alert > .v-icon + .v-alert__content {
  margin-left: 0;
}
.application--is-rtl .v-alert > .v-alert__content + .v-icon {
  margin-left: 0;
}

.v-alert__border {
  border-style: solid;
  border-width: thick;
  content: "";
  position: absolute;
}
.v-alert__border:not(.v-alert__border--has-color) {
  opacity: 0.26;
}
.v-alert__border--left, .v-alert__border--right {
  bottom: 0;
  top: 0;
}
.v-alert__border--bottom, .v-alert__border--top {
  left: 0;
  right: 0;
}
.v-alert__border--bottom {
  border-bottom-left-radius: inherit;
  border-bottom-right-radius: inherit;
  bottom: 0;
}
.v-alert__border--left {
  border-top-left-radius: inherit;
  border-bottom-left-radius: inherit;
  left: 0;
}
.v-alert__border--right {
  border-top-right-radius: inherit;
  border-bottom-right-radius: inherit;
  right: 0;
}
.v-alert__border--top {
  border-top-left-radius: inherit;
  border-top-right-radius: inherit;
  top: 0;
}
.application--is-rtl .v-alert__border--left {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
  border-top-right-radius: inherit;
  border-bottom-right-radius: inherit;
  left: auto;
  right: 0;
}
.application--is-rtl .v-alert__border--right {
  border-top-left-radius: inherit;
  border-bottom-left-radius: inherit;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
  left: 0;
  right: auto;
}

.v-alert__content {
  flex: 1 1 auto;
}

.v-alert__dismissible {
  margin-top: -6px;
  margin-bottom: -6px;
}

.v-alert__icon {
  border-radius: 50%;
  height: 24px;
  min-width: 24px;
  position: relative;
}
.v-alert__icon.v-icon {
  font-size: 24px;
}

.v-alert--border-top {
  padding-top: 24px;
}
.v-alert--border-right {
  padding-right: 24px;
}
.v-alert--border-bottom {
  padding-bottom: 24px;
}
.v-alert--border-left {
  padding-left: 24px;
}
.application--is-rtl .v-alert--border-left {
  padding-right: 24px;
  padding-left: 16px;
}
.application--is-rtl .v-alert--border-right {
  padding-left: 24px;
  padding-right: 16px;
}

.v-alert--dense {
  padding-top: 8px;
  padding-bottom: 8px;
}
.v-alert--dense .v-alert__border {
  border-width: medium;
}

.v-alert--outline {
  background: transparent !important;
  border: 2px solid currentColor !important;
}
.v-alert--outline .v-alert__icon {
  color: inherit !important;
}

.v-alert--prominent {
  align-items: center;
}
.v-alert--prominent .v-alert__icon {
  height: 48px;
  min-width: 48px;
}
.v-alert--prominent .v-alert__icon:after {
  background: currentColor !important;
  border-radius: 50%;
  bottom: 0;
  content: "";
  left: 0;
  opacity: 0.16;
  position: absolute;
  right: 0;
  top: 0;
}
.v-alert--prominent .v-alert__icon.v-icon {
  font-size: 32px;
}

.v-alert--text {
  background: transparent !important;
}